body{
    transition: opacity 0.4s ease-in;
    box-sizing: border-box;
    min-height: 100%;
}

.btn{
    width: 150px;
    height: 50px;
    font-size: 20px;
    font-weight: bold;
    color: rgb(68,68,68);
    text-decoration: none;
    user-select: none;
    padding: .2em 1.2em;
    outline: none;
    border: 1px solid rgba(0,0,0,.1);
    border-radius: 2px;
    background: rgb(245,245,245) linear-gradient(#f4f4f4, #f1f1f1);
    transition: all .218s ease 0s;
}
.btn:hover{
    background: transparent;
    color: white;
}

.img{
    width: 50%;
    margin: 50px;

}
.wrapper{
    width: 100%;
    min-height: 100%;
}

.head h1{
    margin: 0 !important;
}
.box{
    width: 100%;
    background-image: url("/src/images/img/Software.jpg");
    background-size: cover;
    height: 450px;
    color: white;
}

.boxRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.row{
    margin: 100px 100px 0 0;
    text-align: center;
}
.btnBox{
    margin-top: 40px;
}

.fas{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 180px;
    margin:0 auto;
}
a{
    text-decoration: none;
    color: white;
}
.fasIcon{
    cursor: pointer;
    fill-opacity: 80%;
}
.fasIcon:hover {
    fill-opacity: 100%;
}
.info{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 50px 10px;
}
.infoRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 100px;
}
.infoH2{
    font-size: 30px;
    color: white;
    margin-left: 50px;
}
.infoHr{
    width: 400px;
    align: left;
}
.info p{
    padding: 0 30px;
}
.infoAttr{
    text-align: right;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}.infoAttr2{
    text-align: right;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
 .btnLang{
     display: flex;
     flex-direction: row;
 }
 .btnSwitch{
     align-items: center;
     font-weight: 500;
     font-size: 14px;
     display: flex;
     justify-content: center;
     margin: 0 30px;
     width: 100px;
     height: 50px;
     color: rgb(68,68,68);
     text-decoration: none;
     user-select: none;
     padding: .2em 1.2em;
     outline: none;
     border: 1px solid rgba(0,0,0,.1);
     border-radius: 2px;
     background: rgb(245,245,245) linear-gradient(#f4f4f4, #f1f1f1);
     transition: all .218s ease 0s;
     cursor: pointer;
 }
 .btnSwitch:hover{
     background: transparent;
     color: white;
 }
.infoHead{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.infoHead h3{
    padding: 0;
    margin: 0 0 0 20px;
}
.infoFas{
    color: white;
}
.infoFas2{
    color: white;
}
@media screen and (max-width: 550px){
    .boxRow{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }
    .infoHr{
        width: 50%;
    }
    .infoRow p{
        font-size: 15px !important;
    }
    .row{
        margin: 0;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    .box{
        width: 100%;
        background-image: url("/src/images/img/Software.jpg");
        background-size: cover;
        background-position: right;
        height: 450px;
        color: white;

    }
    .info{
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .img{
        margin: 10px 100px;
    }
}